.fg {
  position: relative;
  margin: 0;
  input,
  textarea{
    border: none;
    border-radius: 0.3125em;
    background: rgba(217, 217, 217, 0.33);
    padding: 0 .625em;
    height: 2.6875em;
    width: 100%;
    font-size: 1em;
    color: $black;
    &::placeholder {
      color: rgba($black,.7);
    }
    &:focus {
      background: rgba(217, 217, 217, 0.6);
    }
  }
  textarea{
    padding: .625em;
    height: 8.8125em;
    resize: none;
  }
  .nice-select{
    font-size: 1em;
    width: auto;
    min-width: 100%;
    border-radius: 0.3125em;
    background: rgba(255, 255, 255, 0.25) !important;
    border: none;
    padding: 0  .625em;
    height: 2.125em;
    color: #FFF;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:before{
      content: '';
      flex-shrink: 0;
      order: 3;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(100%);
      margin-left: .25em;
      width: 0.75em;
      height: 0.375em;
    }
    .list{
      background-color: #fff;
    }
    .option{
      border: none;
      font-size: 1em;
      padding: .5em .625em;
      color: $black;
      &:hover,
      &.selected{
        color: #fff;
        font-weight: 400;
        background-color: $black;
      }
      &.disabled{
        display: none;
      }
    }
  }
}

.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
  label{
    position: relative;
    cursor: pointer;
    display: block;
    color: rgba($black,.5);
    span{
      font-size: .875em;
      line-height: 130%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
      background-color: #D9D9D9;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-size: .5625em auto;
      }
    }
  }
  label {
    padding-left: 1.625em;
    min-height: 1em;
    &:before {
      width: 1em;
      height: 1em;
      @include bgImgSize(0);
      background-image: url(../img/check.svg);
      transition: all .0s;
    }
  }
}
