.events2{
  padding: 1.75em 0 2em;
  color: $black;
}

.club-video{
  padding: 1.75em 0;
  .video{
    margin-top: 1.25em;
  }
}

.video{
  display: block;
  position: relative;
  &__play{
    @include centerAbsolute;
    width: 3.625em;
  }
  &__img{
    display: block;
    width: 100%;
    border-radius: .625em;
  }
}

.gallery,
.residents{
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 7.5em / 4));
    gap: 2.5em;
  }
}

.gallery{
  padding: 3em 0 1.75em;
  &__grid{
    margin-top: 2.1875em;
    grid-auto-flow: dense;
  }
}

.gallery-item{
  @include imgCover(100%);
  border-radius: .625em;
  @media screen and (min-width: 577px){
    &._big{
      grid-column: span 2;
      grid-row: span 2;
    }
  }
}

.about-club{
  padding: 2.5em 0 3em;
  .text{
    margin-top: 1.05em;
  }
}

.residents{
  padding: 4em 0;
  background: #F6F6F6;
  &__grid{
    margin-top: 3.56em;
  }
}

.resident-card{
  &__top{
    max-width: 10em;
    margin-bottom: 1.5em;
  }
  &__img{
    @include imgCover(100%);
    border-radius: 50%;
  }
  &__info{
    @include grid100gap(.625em)
  }
  &__desc{
    opacity: .8;
    line-height: 140%;
  }
  &__badge{
    display: inline-flex;
    margin-right: auto;
    padding: .31em;
    border-radius: 0.3125em;
    background: #EBEBEB;
    color: rgba($black,.8);
    span{
      font-size: .9375em;
      line-height: 140%;
    }
  }
}

.president{
  padding: 3.65em 0 1.7em;
  .container{
    display: flex;
    margin-top: 5.06em;
    margin-bottom: 5.06em;
  }
  &__inner{
    width: 100%;
    padding: 2.56em 3.125em 4.94em;
    border-radius: 0.3125em;
    background: #F6F6F6;
    min-height: 24.75em;
    position: relative;
  }
  &__img{
    position: absolute;
    right: 6.25em;
    top: 50%;
    transform: translateY(-50%);
    width: 28.75em;
    border-radius: .3125em;
  }
  &__content{
    max-width: 25.625em;
    @include grid100gap(.875em);
  }
  .text{
    line-height: 140%;
    p{
      @include lastM(0 0 .7em);
    }
  }
}

.about{
  padding: 4em 0 3.65em;
  &__grid{
    margin-top: 2em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
  }
}

.about-card{
  padding: 1.56em 1.25em;
  border-radius: 1.25em;
  background: #F6F6F6;
  &__icon{
    height: 3.75em;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  &__info{
    margin-top: 1.25em;
    @include grid100gap(.75em);
  }
  &__title{
    font-size: 1.875em;
    line-height: 120%;
    font-weight: 700;
  }
  &__desc{
    font-size: .9375em;
    line-height: 140%;
    color: #222;
    opacity: .8;
  }
}

.about-card2{
  padding: 1.56em 1.25em;
  border-radius: 1.25em;
  background: #3B3B3B;
  color: #FFF;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  &__link{
    margin-top: 1.25em;
    padding: 0 1.31em;
    height: 3.3125em;
    border-radius: 0.65406em;
    background: rgba(255, 255, 255, 0.25);
    @include center;
    font-weight: 700;
    color: #fff;
    span{
      font-size: 1.5em;
      line-height: 130%;
      text-decoration: underline;
    }
    &:hover{
      background: rgba(255, 255, 255, 0.5);
      color: #fff;
    }
  }
}



@media screen and (max-width: 992px){
  .events2{
    padding: 2.125em 0 1.5em;
  }

  .club-video{
    padding: 2.125em 0;
    .video{
      margin-top: .875em;
    }
  }

  .video{
    &__play{
      width: 2em;
    }
  }

  .residents,
  .gallery{
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
      gap: 1.25em;
    }
  }

  .gallery{
    padding: 2.125em 0;
    &__grid{
      margin-top: 1.25em;
    }
  }

  .about-club{
    padding: 4.875em 0 2.125em;
    .text40{
      line-height: 120%;
    }
    .text{
      margin-top: 1.33em;
    }
  }

  .residents{
    padding: 2.06em 0 5.38em;
    &__grid{
      margin-top: 1.44em;
    }
    .resident-card{
      &__top{
        max-width: 7.5em;
      }
      &__title{
        font-size: 1em;
        line-height: 119%;
      }
      &__desc{
        font-size: .875em;
      }
    }
  }

  .president{
    padding: .625em 0 1.125em;
    .container{
      margin: 0;
    }
    &__inner{
      padding: 0;
      background-color: transparent;
      min-height: unset;
    }
    &__img{
      position: static;
      transform: unset;
      max-width: 100%;
      margin: 0 auto;
      display: block;
    }
    &__content{
      margin: -7.5em 0 0 -.9375em;
      max-width: unset;
      width: calc(100% + 1.875em);
      gap: .75em;
      padding: 9.81em 1.875em 2.94em;
      background: #F6F6F6;
    }
    .text45{
      max-width: 10.5em;
    }
    .text{
      p{
        @include lastM(0 0 .8125em);
      }
    }
  }

  .about{
    padding: 1.875em 0 .625em;
    &__grid{
      margin-top: 1.31em;
      grid-template-columns: repeat(2,calc(50% - 1.06em / 2));
      gap: 1.06em;
    }
  }

  .about-card{
    padding: 1.75em 1.25em 2em;
    &__icon{
      height: 2.5em;
    }
    &__info{
      gap: .625em;
    }
    &__title{
      font-size: 1.25em;
      line-height: 125%;
    }
  }

  .about-card2{
    padding: 2em 1.25em;
    .about-card__title{
      max-width: 13em;
    }
    &__link{
      margin-top: 1.06em;
    }
  }
}


@media screen and (max-width: 576px){
  .residents,
  .gallery{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
  }

  .about{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
