.contacts{
  padding: 2.38em 0 4.38em;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
}

.contacts-info{
  max-width: 16.25em;
  flex-shrink: 0;
  padding: 3em 0;
  &__top{
    @include grid100gap(1.25em);
  }
  &__items{
    margin-top: 2em;
    @include grid100gap(1.56em);
  }
}

.contacts-item{
  display: flex;
  color: rgba($black,.7);
  &__icon{
    width: 1.5em;
    flex-shrink: 0;
    margin-right: .94em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-size: .8125em;
    line-height: 150%;
  }
  &__desc{
    font-size: 1.125em;
    line-height: 150%;
    font-weight: 700;
  }
}

.contacts-form{
  padding: 2.81em 6.5em 1.44em;
  background: #FFF;
  box-shadow: 0 .6875em 3.4375em 0 rgba(0, 0, 0, 0.12);
  width: 35em;
  .text20{
    text-align: center;
    line-height: 120%;
  }
  &__fields{
    margin-top: 1.75em;
    @include grid100gap(.625em);
  }
  .m-btn{
    height: 2.6875em;
    span{
      font-size: .9375em;
    }
  }
}

.business-club{
  padding: 11em 0;
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__top{
    max-width: 31.25em;
    @include grid100gap(1em);
  }
  &__nav{
    margin-top: 1.56em;
    .m-btn{
      width: 22.125em;
      height: 3.75em;
      max-width: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .contacts{
    padding: 4.06em 0 2.625em;
    &__inner{
      display: block;
    }
  }

  .contacts-info{
    margin: 0 auto;
    padding: 0 0 2.81em;
    &__top{
      text-align: center;
      gap: .44em;
      .text45{
        font-size: 1.875em;
        line-height: 150%;
      }
    }
    &__items{
      gap: 1.25em;
    }
  }

  .contacts-form{
    padding: 0;
    box-shadow: none;
    background-color: transparent;
    width: 100%;
    &__fields{
      margin-top: 1em;
    }
  }

  .business-club{
    padding: 5em 0;
  }
}
