.main{
  padding: 1.75em 0 1.625em;
  @media screen and (min-width: 993px){
    .container{
      padding: 0 1.25em;
    }
  }
  &__inner{
    border-radius: 1.25rem;
    background: #FFF;
    box-shadow: 0 .5em 1.5em 0 rgba(0, 0, 0, 0.10);
  }
}

.main-slider{
  color: #fff;
  position: relative;
  .swiper{
    &-container{
      overflow: hidden;
      border-radius: 1.25em;
      background: $black;
    }
    &-slide{
      padding: 5.19em 4.875em 5.44em;
    }
    &-button{
      position: absolute;
      top: 0;
      height: 100%;
      width: 2.5em;
      @include center;
      margin: 0;
      transition: all .3s;
      z-index: 3;
      &:hover{
        background: rgba(255, 255, 255, 0.10);
      }
      &-prev{
        left: 0;
        border-radius: 1.25em 0 0 1.25em;
      }
      &-next{
        right: 0;
        border-radius: 0 1.25em 1.25em 0;
      }
    }
    &-pagination{
      bottom: 1.69em;
      left: 0;
      width: 100%;
      padding: 0 4.655em;
      display: flex;
      justify-content: unset;
      &-bullet{
        background-color: #fff;
        opacity: .4;
        width: 0.5em;
        height: 0.5em;
        margin: 0 .22em;
        flex-shrink: 0;
        &-active{
          opacity: 1;
        }
      }
    }
  }
  &__text{
    max-width: 35em;
    @include grid100gap(.75em);
  }
}

.cases{
  margin-top: .75em;
}

.case-card{
  padding: 3.25em 2.56em 1.875em;
  display: grid;
  grid-template-columns: 10.4375em 16.125em calc(100% - 42.625em) 10.8125em;
  gap: 1.75em;
  position: relative;
  &:not(:last-child){
    border-bottom: 1px solid rgba($black,.3);
  }
  &._green{
    .case-card__num{
      color: #8BCC74;
    }
  }
  &__col1{
    padding-right: .75em;
  }
  &__title{
    text-transform: uppercase;
    color: rgba($black,.8);
    font-weight: 600;
    min-height: 2.4em;
    a:hover{
      color: $black;
    }
  }
  &__img{
    display: block;
    margin-top: .9375em;
    img{
      width: 100%;
      display: block;
      border-radius: .3125em;
    }
  }
  &__num{
    position: absolute;
    top: 3.125em;
    left: -1.19em;
    font-weight: 900;
    span{
      font-size: 2.0625em;
      line-height: 127%;
    }
  }
  &__badge{
    position: absolute;
    top: .625em;
    left: -.56em;
    height: 1.5625em;
  }
  &__feedback{
    @include grid100gap(.3125em);
    &-item{
      display: flex;
      align-items: center;
      color: rgba($black,.5);
      img{
        width: 1.25em;
        margin-right: .3125em;
      }
      .text12{
        line-height: 117%;
      }
    }
  }
  &__nav{
    padding: .94em 1.06em 0 0;
    .m-btn{
      width: 100%;
      span{
        font-size: .83em;
      }
    }
  }
}

.case-contacts{
  @include grid100gap(.625em);
  &__item{
    font-size: .9375em;
    line-height: 120%;
    color: rgba($black,.8);
    b{
      font-weight: 700;
    }
    a{
      text-decoration: underline;
    }
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    @include center;
    margin-right: .56em;
    svg{
      max-width: 1.5em;
      max-height: 1.25em;
    }
    &:hover{
      path{
        opacity: 1;
      }
    }
  }
}

.text{
  color: rgba($black,.7);
  font-size: .875em;
  line-height: 150%;
  hr{
    display: block;
    margin: 0;
    border: none;
    height: 1.375em;
  }
  h2,h3{
    line-height: 120%;
    font-weight: 700;
    margin-bottom: .5em;
  }
  h2{
    font-size: 2.25em;
  }
  h3{
    font-size: 1.75em;
  }
  p,li,ul,ol{
    line-height: inherit;
  }
  ul,ol,
  p{
    @include lastM(0 0 1.25em);
  }
  a{
    font-weight: 700;
    text-decoration: underline;
  }
  ul{
    padding-left: .625em;
    li{
      padding-left: .86em;
      position: relative;
      &:before{
        content: '•';
        position: absolute;
        top: 0;
        left: 0;
        background-image: unset;
        width: unset;
        height: unset;
      }
    }
  }
  ol{
    counter-reset: section;
    padding-left: .375em;
    li{
      counter-increment: section;
      position: relative;
      padding-left: 1.0625em;
      &:before{
        content: counter(section) '. ';
        @include sameNumWidth;
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 2.5em 0 3.625em;
    &__inner{
      border-radius: 0.625em;
    }
  }

  .main-slider{
    .swiper{
      &-container{
        border-radius: 0.625em;
      }
      &-slide{
        padding: 5.94em 1.875em 4.94em;
      }
      &-button{
        display: none;
      }
      &-pagination{
        padding: 0 1.655em;
        bottom: 1.56em;
      }
    }
  }

  .case-card{
    padding: 1.875em 1.25em;
    grid-template-columns: 100%;
    gap: .9375em;
    &__col1{
      display: grid;
      grid-template-columns: calc(100% - 9.5em) 7.25em;
      gap: 2.25em;
      padding: 0;
    }
    &__col3{
      padding: .9375em 0;
    }
    &__img{
      margin: 0;
    }
    &__top{
      display: flex;
      align-items: center;
      margin-bottom: .81em;
    }
    &__num{
      position: static;
      span{
        font-size: 2.25em;
        line-height: 100%;
      }
    }
    &__badge{
      position: static;
      margin-left: .625em;
    }
    &__nav{
      padding: 1.25em 0 0;
    }
  }

  .case-contacts{
    gap: .9375em;
  }

  .text{
    h2,h3{
      font-size: 1.67em;
    }
  }
}
