.params{
  padding: 5.625em 0 4em;
  &__inner{
    background: #212121;
    box-shadow: 0 .6875em 7.6875em rgba(0, 0, 0, 0.10);
    padding: 2.5em 2.75em;
    border-radius: 1.25em;
    color: #FFF;
  }
  &__grid{
    margin-top: 1.81em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
  &__stat{
    &-items{
      margin-top: 1.56em;
      display: flex;
      justify-content: space-between;
    }
  }
  &__bottom{
    margin-top: 2.81em;
    display: flex;
    .feedback{
      @include lastM(0 2.44em 0 0);
    }
  }
}

.param-card{
  background-color: rgba(#fff,.1);
  border-radius: 0.625em;
  padding: 1.25em;
  min-height: 8.8125em;
  &__btn{
    padding: 0 .625em;
    height: 2.4375em;
    border-radius: 0.3125em;
    background: #FFF;
    color: rgba(#333,.8);
    &:hover{
      color: #FFF;
      background: $black;
    }
  }
  &__links{
    display: flex;
    flex-wrap: wrap;
    margin-top: .07em;
    .param-card__btn{
      margin: .81em .81em 0 0;
    }
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.format{
  &__links{
    margin-top: 1.5em;
    display: flex;
  }
  &__link{
    @include lastM(0 1.31em 0 0);
    border-bottom: 1px solid #fff;
    transition: all .3s;
    &:not(._active){
      cursor: pointer;
      opacity: .6;
      border-bottom-color: transparent;
    }
    &:hover{
      opacity: 1;
    }
    span{
      font-size: 1em;
      line-height: 119%;
    }
  }
  &__props{
    margin-top: 1.81em;
    @include grid100gap(1.06em);
  }
  &__prop{
    display: flex;
    justify-content: space-between;
    &-title,
    &-desc{
      font-size: 1em;
      line-height: 119%;
    }
    &-title{
      font-weight: 700;
    }
    &-desc{
      text-align: right;
    }
  }
  &__nav{
    margin-top: 1.625em;
    display: flex;
  }
}

.address{
  &__links{
    margin-top: .3125em;
    display: flex;
    flex-wrap: wrap;
  }
  &__link{
    margin: .3125em .625em 0 0;
    border-bottom: 1px solid #fff;
    &:not(._active){
      cursor: pointer;
      opacity: .55;
      border-bottom-color: transparent;
    }
    &:hover{
      opacity: 1;
    }
    span{
      font-size: .9375em;
      line-height: 140%;
    }
  }
  &__filials{
    margin-top: .625em;
    @include grid100gap(.3125em);
  }
  &__street{
    display: flex;
    align-items: center;
    padding: .44em .625em;
    border-radius: 0.3125em;
    background: rgba(255, 255, 255, 0.20);
    img{
      width: 1.125em;
      margin-right: .625em;
    }
  }
}

.stat-item{
  padding: 1.06em 1.13em .875em 1.25em;
  flex-grow: 1;
  max-width: 16.625em;
  min-height: unset;
  @include grid100gap(.31em);
  &:nth-child(odd){
    margin-right: 1.25em;
  }
}

.feedback{
  @include grid100gap(.44em);
  min-width: 16.4375em;
  .text30{
    line-height: 120%;
  }
  &__num{
    color: #5DCF35;
    font-size: 4.6875em;
    line-height: 121%;
    font-weight: 700;
  }
}

.club{
  background-size: cover;
  background-position: center;
  color: #fff;
  &:after{
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.77) 0%, rgba(0, 0, 0, 0.77) 30.35%, rgba(0, 0, 0, 0.00) 100%);
  }
  &__inner{
    position: relative;
    z-index: 2;
    min-height: 37.5em;
    padding: 2.25em 0;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__img{
    display: block;
    width: 10em;
    border-radius: .3125em;
    margin-bottom: 1.31em;
  }
  .text{
    margin-top: .75em;
    color: #fff;
  }
}

.club-contacts{
  @include grid100gap(1.31em);
  padding-bottom: 3.6em;
  &__group{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .about-card2__link{
    margin-top: .19em;
    padding: 0 .625em;
    height: 2.75em;
    border-radius: .3125em;
    span{
      font-size: 1.25em;
    }
  }
  &__link{
    font-weight: 700;
    margin-top: .44em;
    span{
      font-size: 1.25em;
      line-height: 140%;
    }
  }
  .socials{
    margin-top: .56em;
    &__item{
      margin-right: .91em;
      svg{
        max-width: 2.4375em;
        max-height: 2.0625em;
      }
    }
  }
}

.ceo{
  padding: 1.625em 0 5em;
  overflow: hidden;
}

.ceo-slider{
  margin-top: 3em;
  .swiper{
    &-slide{
      width: 18.75em;
      padding-right: 2.5em;
    }
    &-nav{
      display: flex;
      align-items: center;
      margin-top: 2.31em;
    }
    &-button{
      position: static;
      margin: 0 .56em 0 0;
      width: 3.6875em;
      height: 2.875em;
      border-radius: 0.3125em;
      border: 1px solid rgba(200, 200, 200, 0.25);
      background: #C8C8C8;
      svg{
        width: 1.8125em;
      }
      &:hover{
        background: $black;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .params{
    padding: 4.06em 0 3.75em;
    .container{
      padding: 0;
    }
    &__inner{
      padding: 1.56em .9375em;
      border-radius: 0;
    }
    &__top{
      padding: 0 1.56em;
    }
    &__grid{
      margin-top: 1.44em;
      grid-template-columns: 100%;
    }
    &__stat{
      &-items{
        display: none;
      }
    }
  }

  .feedback{
    gap: 0;
    min-width: unset;
    .text30{
      font-size: 1.25em;
    }
  }

  .club{
    margin: 1.31em auto 0;
    width: calc(100% - 1.875em);
    border-radius: 1.25em;
    &__inner{
      display: block;
      padding-top: 8.63em;
    }
    &__img{
      width: 5.9375em;
      margin-bottom: 1em;
    }
    .text{
      margin-top: 1.06em;
    }
  }

  .club-contacts{
    margin-top: .13em;
    display: flex;
    flex-wrap: wrap;
    gap: unset;
    padding: 0;
    &__group{
      margin: 1.31em 1.31em 0 0;
    }
  }

  .ceo{
    padding: 1.125em 0 4.875em;
  }

  .ceo-slider{
    margin-top: 1.5em;
    .swiper{
      &-slide{
        width: 17.5em;
        padding-right: 1.25em;
      }
      &-nav{
        margin-top: 2.625em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .param-card{
    padding: 1.31em .8em;
  }

  .format{
    &__prop{
      &-title,
      &-desc{
        font-size: .875em;
      }
    }
  }
}
