.advertising{
  padding: 4.25em 0 8em;
  &__links{
    display: flex;
    flex-wrap: wrap;
  }
  &__link{
    margin: .625em .625em 0 0;
    padding: 0 .375em;
    height: 2.0625em;
    display: flex;
    align-items: center;
    border-radius: 34.6875em;
    background: #DCDCDC;
    color: rgba($black,.7);
    span{
      font-size: .9375em;
      line-height: 150%;
    }
  }
  &__text{
    max-width: 46.5em;
    margin-top: 1.56em;
    .text{
      font-size: .9375em;
      h3{
        font-size: 1.33em;
      }
      a{
        font-weight: inherit;
      }
      img{
        width: calc(100% + 1.5em);
        margin-left: -1.5em;
        margin-bottom: 1.5em;
        display: block;
        &:last-child{
          margin-bottom: 0;
        }
      }
    }
  }
}

.advertising-form{
  margin-top: 4.19em;
  max-width: 42.1875em;
  .text30{
    line-height: 120%;
  }
  &__fields{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .4em));
    gap: .69em .8em;
    ._big{
      grid-column: 2/3;
      grid-row: 1/4;
      textarea{
        min-height: 100%;
      }
    }
  }
  .fg{
    input{
      height: 3.5625em;
      padding-left: 2.37em;
    }
    textarea{
      padding-top: 1.06em;
      padding-left: 2.375em;
    }
    &__icon{
      position: absolute;
      top: 1.09375em;
      left: .625em;
      height: 1.375em;
      width: 1.25em;
      display: flex;
      align-items: center;
      img{
        max-width: 100%;
        max-height: 100%;
        display: block;
      }
    }
  }
  .m-check{
    margin-top: .5em;
  }
  .m-btn{
    margin-top: .5em;
    grid-column: 1/-1;
  }
}

._full{
  grid-column: 1/-1;
}


@media screen and (max-width: 992px){
  .advertising{
    padding: 4.06em 0 5.81em;
    &__links{
      margin-top: .375em;
    }
  }
}


@media screen and (max-width: 576px){
  .advertising-form{
    &__fields{
      grid-template-columns: 100%;
    }
    ._big{
      grid-row: unset;
      grid-column: 1/-1;
    }
    .fg{
      input{
        height: 2.6875em;
      }
      textarea{
        padding-top: .625em;
      }
      &__icon{
        top: .65625em;
        width: 1.125em;
      }
    }
    .m-check{
      margin-top: 0;
    }
    .m-btn{
      height: 2.6875em;
      margin-top: 0;
      span{
        font-size: .9375em;
      }
    }
  }
}
