@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.44em;
    height: 3.375em;
    border-radius: 0.3125em;
    &:focus {
        box-shadow: none;
    }
    &._small{
        height: 2.375em;
        padding: 0 .625em;
        span{
            font-size: .9375em;
        }
    }
    span{
        font-size: 1em;
        line-height: 119%;
    }
}

.m-btn-black{
    background: #202020;
    color: #FFF;
    font-weight: 700;
    &:hover{
        background: lighten(#202020,10%);
        color: #FFF;
    }
}
