.news{
  padding: 5.625em 0 6.31em;
  &__top{
    margin-top: 1em;
    display: flex;
    justify-content: space-between;
  }
  &__desc{
    max-width: 47.5em;
    .text15{
      line-height: 140%;
    }
  }
  .news-items{
    margin-top: 2.25em;
  }
  .search-field{
    width: 16.25em;
    max-width: 100%;
  }
}

.search-field{
  input{
    width: 100%;
    height: 2.5em;
    border-radius: 0.3125em;
    background-color: rgba(12, 12, 12, 0.11);
    background-image: url(../img/search-icon.svg);
    background-size: 1.25em auto;
    background-repeat: no-repeat;
    background-position: left .625em center;
    border: none;
    padding: 0 1em 0 2.5em;
    font-size: 1em;
    line-height: 120%;
    &::placeholder{
      color: #333;
    }
  }
}

.pagination{
  display: flex;
  margin-top: 2.5em;
  &__item{
    width: 3.125em;
    height: 3.125em;
    @include center;
    font-weight: 700;
    border-radius: 0.25em;
    color: rgba($black,.4);
    span{
      font-size: .9375em;
      line-height: 120%;
    }
    &:hover,
    &._active{
      background-color: $black;
      color: #fff;
    }
  }
}

.news-item{
  min-height: 39.5em;
  display: flex;
  align-items: flex-end;
  color: #fff;
  padding: 4.78em 0;
  &__inner{
    width: 100%;
    position: relative;
    z-index: 2;
  }
}

.m-article{
  padding: 5em 0;
  &__text{
    max-width: 60em;
  }
  &__img,
  &__video{
    position: relative;
    @include lastM(0 0 1.5625em);
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
    &-text{
      position: absolute;
      right: 0;
      bottom: 0;
      padding: 0 2.44em;
      height: 4.625em;
      background-color: #fff;
      color: $gray;
      @include center;
      span{
        font-size: 1.25em;
        line-height: 125%;
      }
    }
  }
  &__cards{
    margin: 3.125em 0;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 1.5em;
  }
  &__video{
    padding-bottom: 56%;
    iframe{
      top: 0;
      left: 0;
      position: absolute;
      width: 100%;
      height: 100%;
      display: block;
    }
  }
}

.article-card{
  &__img{
    width: 3.5em;
    height: 3.5em;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }
  &__info{
    margin-top: 1.5625em;
    @include grid100gap(.3125em);
  }
}



@media screen and (max-width: 992px){
  .news{
    padding: 4.44em 0 5.31em;
    .text45{
      padding: 0 1em;
    }
    &__top{
      display: block;
      padding: 0 1.5625em;
    }
    .news-items{
      margin-top: 1.75em;
    }
    .search-field{
      width: 100%;
      margin-top: 1.75em;
    }
  }

  .pagination{
    display: none;
  }

  .news-item{
    min-height: calc(100 * var(--vh) - 2.875em);
    padding: 5.75em 0;
  }

  .m-article{
    padding: 2.875em 0 6.61em;
    &__img{
      &-text{
        height: 2.5em;
        padding: 0 1em;
        span{
          font-size: .875em;
        }
      }
    }
    &__cards{
      margin: 2.5em 0;
      grid-template-columns: repeat(2,calc(50% - 1.25em));
      gap: 2.5em;
    }
  }

  .article-card{
    .text20{
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .m-article{
    &__cards{
      grid-template-columns: 100%;
    }
  }
}
