.calendar{
  background-color: rgba($black,.9);
  padding: 3.69em 0;
  color: #fff;
  &__inner{
    display: grid;
    grid-template-columns: 35.25em calc(100% - 55.75em) 16.25em;
    gap: 2.625em;
  }
  &__top{
    padding-bottom: .75em;
    margin-bottom: 1.25em;
    border-bottom: 1px solid rgba(#fff,.3);
  }
  &__theme{
    @include grid100gap(1.25em);
  }
  &__groups{
    @include grid100gap(1.56em);
  }
  .m-check{
    label{
      padding-left: 1.875em;
      color: #979797;
      &:before{
        background-color: rgba(#D9D9D9,.5);
      }
      span{
        font-size: 1em;
        line-height: 125%;
      }
    }
    input:checked~label{
      color: #fff;
    }
  }
}

.calendar-item{
  margin-top: 1.31em;
  input{
    opacity: 0;
    pointer-events: none;
    position: absolute;
  }
}

.air-datepicker{
  width: 100%;
  max-width: 30em;
  font-family: $font1;
  font-size: 1em;
  background-color: transparent;
  border: none;
  &-nav{
    max-width: 28em;
    border-bottom: none;
    padding: 0;
    align-items: center;
    margin-bottom: 1.31em;
    &--title{
      font-size: 1.25em;
      font-weight: 400;
      color: #fff;
      &:hover{
        background-color: transparent;
      }
    }
    &--action{
      width: .875em;
      height: .9375em;
      @include bgImgSize(100%);
      opacity: .4;
      svg{
        display: none;
      }
      &[data-action="prev"]{
        background-image: url(../img/prev.svg);
      }
      &[data-action="next"]{
        background-image: url(../img/next.svg);
      }
      &:hover{
        background-color: transparent;
        @include bgImgSize(100%);
        opacity: 1;
      }
    }
  }
  &--content{
    padding: 0;
  }
  &-body{
    &--cells{
      grid-auto-rows: unset !important;
      gap: .625em;
      &.-days-{
        grid-template-columns: repeat(7,calc(100% / 7 - 3.75em / 7));
      }
      &.-months-{
        grid-template-columns: repeat(3,calc(100% / 3 - 1.25em / 3));
      }
      &.-years-{
        grid-template-columns: repeat(4,calc(25% - 1.875em / 4));
      }
    }

    &--day-names{
      display: none;
    }
  }
  &-cell{
    color: #FFF;
    font-size: 1.25em;
    height: 3em;
    border-radius: 0.3125rem;
    border: 1px solid rgba(255, 255, 255, 0.20);
    background: #2A2A2A;
    box-shadow: 0 .6875em 1.375em 0 rgba(0, 0, 0, 0.12);
    &.-current-.-selected-,
    &.-selected-,
    &:hover{
      background: #FFF7F7;
      color: $black !important;
    }
    &.-other-month-{
      display: none;
    }
  }
}

.calendar-group{
  @include grid100gap(.625em);
  &__title{
    font-size: 1.25em;
    line-height: 150%;
    font-weight: 700;
  }
  &__checks{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1em));
    gap: .5em;
  }
}

.events{
  padding: 3.875em 0 6.75em;
  color: #333;
  &__top{
    display: flex;
    align-items: flex-end;
  }
  &__desc{
    margin: 0 0 .5em 1em;
    opacity: .6;
    .text14{
      line-height: 150%;
    }
  }
  &__items{
    margin-top: .75em;
  }
}

.event-item{
  padding: 1.25em 0;
  border-bottom: 1px solid rgba($black,.3);
  display: grid;
  grid-template-columns: 9em calc(100% - 44em) 8em 11em 6em;
  gap: 2.5em;
  &__title{
    opacity: 0.6;
    margin-bottom: .625em;
    .text14{
      line-height: 150%;
    }
  }
  &__desc{
    margin-top: .625em;
  }
}


@media screen and (max-width: 992px){
  .calendar{
    &__inner{
      grid-template-columns: repeat(2,calc(50% - .9em));
      gap: 1.8em;
    }
    @media screen and (min-width: 577px){
      &__col2{
        order: 2;
        grid-column: 1/-1;
      }
      &__theme{
        grid-template-columns: repeat(4,calc(25% - .9375em * 3 / 4));
        gap: .9375em;
      }
    }
  }

  .air-datepicker{
    max-width: 21.5625em;
    &-body{
      &--cells{
        gap: .3125em;
        &.-days-{
          grid-template-columns: repeat(7,calc(100% / 7 - 1.875em / 7));
        }
        &.-months-{
          grid-template-columns: repeat(3,calc(100% / 3 - .625em / 3));
        }
        &.-years-{
          grid-template-columns: repeat(4,calc(25% - .3125em * 3 / 4));
        }
      }
    }
    &-cell{
      height: 2.25em;
    }
  }

  .events{
    padding: 2.25em 0 5em;
    &__top{
      display: block;
    }
    &__desc,
    &__items{
      margin: 0;
    }
  }

  .event-item{
    grid-template-columns: repeat(4,calc(25% - .375em / 4));
    gap: 1.25em .125em;
    &__col2{
      order: -1;
      grid-column: 1/-1;
    }
  }
}


@media screen and (max-width: 576px) {
  .calendar {
    padding: 3.94em 0 2.06em;
    &__inner {
      grid-template-columns: 100%;
    }
    &__col1{
      .text30{
        text-align: center;
      }
    }
    &__top{
      padding-bottom: 1.06em;
      margin-bottom: 1.375em;
      .text30{
        font-size: 1.25em;
      }
    }
    &__theme{
      grid-template-columns: repeat(2,calc(50% - .6875em));
      gap: .9375em 1.375em;
    }
  }

  .event-item{
    grid-template-columns: repeat(2,calc(50% - .0625em));
  }
}
