.what-is{
  padding: 1.625em 0;
  text-align: center;
  .text{
    margin-top: 1.5em;
  }
}

.text2{
  font-size: 1.25em;
  line-height: 150%;
}

.about-rating{
  padding: 1.625em 0;
  .text45{
    text-align: center;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 6.375em / 4));
    gap: 2.125em;
    margin-top: 1.875em;
  }
}

.rating-advantage{
  padding: 2.125em 1em 2.68em;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 1.25em;
  background: #F0F0F0;
  color: rgba($black,.7);
  &__icon{
    height: 2.5em;
    img{
      display: block;
      max-width: 2.5em;
      max-height: 100%;
    }
  }
  &__info{
    margin-top: 1.5em;
    @include grid100gap(.31em);
  }
  &__title{
    font-size: 1.25em;
    line-height: 150%;
    font-weight: 700;
  }
  &__desc{
    font-size: .9375em;
    line-height: 150%;
  }
}

.news-collection{
  padding: 3.75em 0 6.25em;
  .news-items{
    margin-top: 1.25em;
  }
}

.news-items{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1.25em));
  gap: 2.5em;
  &._col3{
    @media screen and (min-width: 993px){
      grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
      .news-card__img{
        height: 15em;
      }
    }
  }
}

.news-card{
  border-radius: .375em;
  background: #FFF;
  box-shadow: 0 .6875em 3.4375em 0 rgba(0, 0, 0, 0.12);
  display: flex;
  flex-direction: column;
  color: #555;
  &__img{
    border-radius: .375em .375em 0 0;
    @include imgCover(48.75%);
  }
  &__content{
    padding: 1.25em;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
  &__info{
    @include grid100gap(.875em);
  }
  &__author{
    display: inline-block;
    margin-right: auto;
    padding: .6875em;
    background: #E4E4E4;
  }
  &__nav{
    margin-top: auto;
    padding-top: .875em;
    display: flex;
  }
}


@media screen and (max-width: 992px){
  .what-is{
    padding: 3.625em 0;
  }

  .text2{
    font-size: .9375em;
  }

  .about-rating{
    padding: 3.625em 0 1.875em;
    &__items{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
      margin-top: 1.25em;
      padding: 0 3em;
    }
  }

  .news-collection{
    padding: 1.875em 0 7.25em;
    .news-items{
      margin-top: 1.625em;
    }
  }

  .news-items{
    @media screen and (min-width: 577px){
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .about-rating{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .news-collection{
    .text45{
      text-align: center;
    }
  }

  .news-items{
    grid-template-columns: 100%;
  }
}
