.logo{
  display: block;
  width: 16.75em;
  flex-shrink: 0;
  img{
    width: 100%;
    display: block;
  }
  ._view2{
    display: none;
  }
}

.header{
  background: #FFF;
  box-shadow: 0 1.375em 4.125em 0 rgba(0, 0, 0, 0.10);
  &__inner{
    justify-content: space-between;
  }
  .logo{
    margin-right: 6.5em;
  }
}

._flex{
  display: flex;
  align-items: center;
}

.menu{
  flex-grow: 1;
  justify-content: space-between;
  &__btn{
    margin-left: 2em;
    img{
      width: 1.5em;
      margin-right: .625em;
    }
  }
}

.nav{
  &__link{
    @include lastM(0 1.875em 0 0);
    color: rgba($black,.4);
    &._active,
    &:hover{
      color: $black;
    }
    span{
      font-size: .875em;
      line-height: 130%;
    }
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .logo{
    width: 11.4375em;
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }

  ._open-menu{
    .menu{
      transform: translateX(0);
    }
    .burger{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }

  .header{
    padding: .1875em 0;
    &__inner{
      justify-content: space-between;
    }
    .logo{
      margin: 0;
      position: relative;
      z-index: 21;
    }
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: #fff;
    overflow-y: auto;
    flex-direction: column;
    align-items: flex-start;
    transform: translateX(-120%);
    z-index: 20;
    padding: 5em .9375em 2em;
    transition: all .3s;
    &__btn{
      margin: 3em 0 0;
    }
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    &__link{
      @include lastM(0 0 1.25em);
      span{
        font-size: 1em;
      }
    }
  }

  .burger{
    @include center;
    width: 2.625em;
    height: 2.41875em;
    border-radius: 0.3125em;
    position: relative;
    z-index: 21;
    img{
      @include centerAbsolute;
      width: 1.375em;
    }
    ._view2{
      width: 1.75em;
      opacity: 0;
    }
  }
}
