.footer{
  padding: 3.25em 0;
  background: $black;
  color: rgba(#fff,.46);
  a:hover{
    color: #fff;
  }
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__col1{
    width: 17.3125em;
    flex-shrink: 0;
    margin-top: -1.125em;
  }
  .logo{
    display: flex;
    align-items: center;
    height: 2.5em;
    width: 13.9em;
  }
  &__info{
    margin-top: 1.25em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__rating,
  &__c{
    @include lastM(0 0 .81em);
    span{
      font-size: .875em;
      line-height: 121%;
    }
  }
  &__conf{
    margin-top: 2em;
    span{
      font-size: .75em;
      line-height: 117%;
    }
  }
  &__title{
    color: #fff;
  }
  &__hint{
    margin-top: 1em;
    max-width: 21.0625em;
    span{
      font-size: .875em;
      line-height: 140%;
    }
  }
  &__contacts{
    margin-top: 1.56em;
    display: flex;
    align-items: center;
  }
  &__telegram{
    padding: 0 .31em;
    height: 1.5em;
    border-radius: 20.8125em;
    background: #3560CF;
    color: #fff;
    @include center;
    margin-left: .31em;
    span{
      font-size: .75em;
      line-height: 120%;
    }
    &:hover{
      background: darken(#3560CF,10%);
      color: #fff;
    }
  }
}

.footer-nav{
  &__links{
    margin-top: 1.44em;
    column-count: 2;
    column-gap: .25em;
    max-width: 20.5em;
  }
  &__link{
    display: inline-block;
    @include lastM(0 0 .94em);
    width: 100%;
    color: rgba(#fff,.7);
    span{
      font-size: .9375em;
      line-height: 114%;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 3.25em 0 4.75em;
    &__inner{
      flex-direction: column;
      position: relative;
      padding-bottom: 5em;
    }
    .logo{
      width: 11.4375em;
    }
    &__col2{
      order: 2;
      margin-top: 2em;
    }
    &__col3{
      .footer__title{
        display: none;
      }
    }
    &__info{
      display: none;
    }
    &__hint{
      margin-top: 1.74em;
      max-width: 100%;
    }
    &__contacts{
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }

  .footer-nav{
    &__links{
      margin-top: .5em;
      column-count: 1;
      column-gap: unset;
      display: flex;
      flex-wrap: wrap;
      max-width: unset;
      width: calc(100% + .625em);
    }
    &__link{
      width: auto;
      margin: .625em .625em 0 0 !important;
      padding: 0 .625em;
      height: 2.3125em;
      border-radius: 20.8125em;
      background: rgba(255, 255, 255, 0.11);
      display: flex;
      align-items: center;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__hint{
      max-width: 18.25em;
      span{
        font-size: .75em;
      }
    }
    &__title{
      font-weight: 400;
      color: rgba(#fff,.5);
      font-size: .75em;
    }
    &__telegram{
      span{
        font-size: .9375em;
      }
    }
  }
}
